<template >
  <div class="blog-toc-container">
    <h3></h3>
    <!-- hash -->
    <RightList :list="tocWithSelect" @select="handleSelect"/> 
  </div>
</template>
<script>
import RightList from "./RightList";
import { debounce } from "@/utils";
export default {
  components:{
    RightList,
  },
  props:{
    toc:{
      type:Array,
      required:true,
    }
  },
  data(){
    return{
      activeAnchor:'',//
    }
  },
  computed:{
    // tocactiveAnchorisSelect
    tocWithSelect(){
      const getToc = (toc=[])=>{
        return toc.map(t=>({
          ...t,
          isSelect:this.activeAnchor === t.anchor,//
          children:getToc(t.children),//children
        }));
      }
      return getToc(this.toc);//()
    },
    // dom
    domsArr(){
      const doms = [];
      const addToDoms = (toc)=>{
        for(const t of toc){
          doms.push(document.getElementById(t.anchor));
          // t0
          if(t.children && t.children.length){
            addToDoms(t.children);
          }
        }
      }
      addToDoms(this.toc);
      return doms;
    }
  },
  //  mainScroll  
  created(){
    // (this deSetSeletct,)
    this.debSetSeletct = debounce(this.setSelect,100);
    // mainScroll
    this.$bus.$on("mainScroll",this.debSetSeletct); 
  },
  destoryed(){
    this.$bus.$off("mainScroll",this.debSetSeletct); 
  },
   methods:{
    handleSelect(item){
      // hash
        location.hash = item.anchor;
    },
    setSelect(){
      this.activeAnchor="";
      const range =  200;//
      for(const dom of this.domsArr){
        // domnullundefined
        if(!dom) {continue;}
        // 
        const top = dom.getBoundingClientRect().top;
        if(top >=0 && top <= range){
          // ,activeAnchordom.id
          this.activeAnchor = dom.id;
          return;
        }else if(top > range){
          // 
          return;
        }else{
          // :
          this.activeAnchor = dom.id;
        }
      }
    }
  },
};
</script>

<style lang="less" scoped>
  .blog-toc-container{
    position: relative;
    width: 200px;
    height: 100%;
    overflow-y: auto;
  }
  @media screen and (max-width:600px) {
    .blog-toc-container{
      display: none;
    }
  }
</style>