<template>
<!-- 1 -->
  <div class="pager-container" v-if="pageNumber > 1">
    <a 
      class="firstPage"
      :class="{ disabled: current === 1 }" 
      @click="handleClick(1)"
    >First</a>
    <!-- current === 1, disabled -->
    <a 
      :class="{ disabled: current === 1 }"
      @click="handleClick(current - 1)"
    >&lt;&lt;</a>

    <a 
      v-for="(n, i) in numbers" 
      :key="i" 
      :class="{ active: n === current }"
      @click = handleClick(n)
    >
      {{ n }}
    </a>

    <a 
      :class="{ disabled: current === pageNumber }"
      @click="handleClick(current + 1)"  
    >&gt;&gt;</a>
    <a 
      class="lastPage"
      :class="{ disabled: current === pageNumber }"
      @click="handleClick(pageNumber)"  
    >Last</a>
  </div>
</template>

<script>
export default {
  // 
  props: {
    current: {
      //
      type: Number,
      default: 1,
    },
    total: {
      //
      type: Number,
      default: 0,
    },
    limit: {
      //
      type: Number,
      default: 10,
    },
    visibleNumber: {
      //
      type: Number,
      default: 10,
    },
  },
  // 
  computed: {
    pageNumber() {
      // =  / 
      return Math.ceil(this.total / this.limit);
    },
    visibleMin() {
      //
      let min = this.current - Math.floor(this.visibleNumber / 2);
      if (min < 1) {
        min = 1;
      }
      return min;
    },
    visibleMax() {
      //
      let max = this.visibleMin + this.visibleNumber - 1;
      if (max > this.pageNumber) {
        max = this.pageNumber;
      }
      return max;
    },
    numbers() {
      // 
      let nums = [];
      for (let i = this.visibleMin; i <= this.visibleMax; i++) {
        nums.push(i);
      }
      return nums;
    },
  },
  methods: {
    handleClick(newPage) {
      if(newPage < 1){ newPage = 1 }  //1
      if(newPage > this.pageNumber){ newPage = this.pageNumber }//
      if(newPage === this.current) return; //
      /*  
          pageChange
          pageChange(newPage)
      */
      this.$emit("pageChange",newPage);
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less"; //srcstyles/var.less
.pager-container {
  display: flex;
  justify-content: center;
  margin: 20px 5vw;
  a {
    flex: 1 1 auto;
    color: @primary;
    margin: 0 1vw;
    cursor: pointer;
    &.disabled {
      color: @lightWords;
      cursor: not-allowed;
    }
    &.active {
      color: @words;
      font-weight: bold;
      cursor: text;
    }
  }
}
@media screen and (max-width:600px) {
  .firstPage,.lastPage{
    display:none;
  }
}
</style>