<template>
  <div class="blog-detail-container" ref="blogDetail">
    <!-- blog blog="oneBlogData" -->
    <h1>{{ blog.title }}</h1>
    <div class="content">
      <span>:{{ formatDate(blog.createDate) }}</span>
      <span>:{{ blog.scanNumber }}</span>
      <a href="#comment-container">{{ blog.commentNumber }}</a>
      <RouterLink
        :to="{
          name: 'CategoryBlog',
          params: {
            categoryId: blog.category.id,
          },
        }"
      >
        {{ blog.category.name }}
      </RouterLink
      >
    </div>
    <!-- blog.htmlContentvue -->
    <div v-html="blog.htmlContent" class=".markdown-body"></div>
  </div>
</template>
<script>
import { formatDate } from "@/utils";
import "highlight.js/styles/github.css"; //
import toTop from "@/mixins/toTop";
export default {
  mixins:[toTop("blogDetail")],//
  // blog
  props: {
    blog: {
      type: Object,
      required: true,
    },
  },
  methods: {
    formatDate,
  },
};
</script>
<style scoped lang="less">
@import "~@/styles/var.less";
.blog-detail-container {
  .content {
    font-size: 13px;
    color: @gray;
    span,
    a {
      margin-right: 10px;
    }
  }
}
</style>