<template>
  <div class="image-loader-container">
    <!-- v-if :  -->
    <img 
      v-if="!everythingDone" 
      class="placeholder" 
      :src="placeholder" 
    />
    <img
      @load="handleLoad"
      :src="src"
      :style="{
        opacity: originOpacity,
        transition: `${duration}ms`,
      }"
    />
  </div>
</template>

<script>
export default {
  props: {
    placeholder: {
      type: String,
      required: true,
    },
    src: {
      type: String,
      required: true,
    },
    duration: {
      type: Number,
      default: 1000,
    },
  },
  data(){
    return {
      originLoaded:false,//
      everythingDone:false,//
    }
  },
  computed:{
    originOpacity(){
      return this.originLoaded ? 1 : 0;//1
    }
  },
  methods:{
    // @load
    handleLoad(){
      this.originLoaded = true;//true
      setTimeout(()=>{//opacity1
        this.everythingDone = true;
        this.$emit("load");// load 
      },this.duration);
    }
  }
};
</script>
<style lang="less" scoped>
@import  "~@/styles/mixin.less";
.image-loader-container{
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  img{
    .self-fill(absolute);
    object-fit: cover;
  }
  .placeholder{
    fill: blur(2vw);
  }
}

</style>

## 

|       |                                          |    |  |  |
| ----------- | ------------------------------------------- | ------ | ---- | ------ |
| src         |                                | String |    |      |
| placeholder |                   | String |    |      |
| duration    |     | Number |    | 500    |

## 

|  |                    |   |  |
| ------ | --------------------- | -------- | -------- |
| load   |   |        |        |