<template>
  <div class="blog-list-container" ref="blogList">
    <!-- Emptydata.rowsdata.rows.length' -->
    <Empty v-if="data.rows && data.rows.length === 0 && !isLoading" />
    <ul>
      <li v-for="item in data.rows" :key="item.id">
        <!-- div -->
        <div class="thumb" v-if="item.thumb">
          <RouterLink :to="{ name: 'Detail', params: { id: item.id } }">
            <img :src="item.thumb" :alt="item.title" :title="item.title" />
          </RouterLink>
        </div>
        <!-- <div class="main"> -->
        <RouterLink :to="{ name: 'Detail', params: { id: item.id } }">
          <h2>{{ item.title }}</h2>
        </RouterLink>
        <div class="message">
          <span>{{ formatDate(item.createDate) }}</span>
          <span>{{ item.scanNumber }}</span>
          <span>:{{ item.commentNumber }}</span>
          <RouterLink
            :to="{
              name: 'CategoryBlog',
              params: { categoryId: item.category.id },
            }"
          >
            {{ item.category.name }}
          </RouterLink>
        </div>
        <div class="desc">
          {{ item.description }}
        </div>
        <!-- </div> -->
      </li>
    </ul>
    <Loading v-if="isLoading" />
    <!-- routeInfo -->
    <Pager
      :current="routeInfo.page"
      :total="data.total"
      :limit="routeInfo.limit"
      :visibleNumber="10"
      @pageChange="handlePageChange"
    />
  </div>
</template>

<script>
import Pager from "@/components/Pager";
import Loading from "@/components/Loading";
import { getBlogs } from "@/api/blog"; //
import { formatDate } from "@/utils";
import toTop from "@/mixins/toTop";
import Empty from "@/components/Empty";
export default {
  mixins: [toTop("blogList")],
  components: {
    Pager,
    Loading,
    Empty,
  },
  data() {
    return {
      data: {}, //
      isLoading: true,
      formatDate, //
    };
  },

  computed: {
    // urlthis.routeInfo
    routeInfo() {
      const categoryId = +this.$route.params.categoryId || -1; //id,-1:
      const page = +this.$route.query.page || 1; //1
      const limit = +this.$route.query.limit || 10; //10
      return {
        categoryId,
        page,
        limit,
      };
    },
  },
  async created() {
    // getBlogs()routInfo
    this.data = await getBlogs(
      this.routeInfo.categoryId,
      this.routeInfo.page,
      this.routeInfo.limit
    );
    // console.log(this.data);
    //Loading
    if (this.data) {
      this.isLoading = false;
    }
  },
  methods: {
    // ,newPage:
    // push()
    handlePageChange(newPage) {
      const query = {
        page: newPage,
        limit: this.routeInfo.limit,
      };
      // 
      if (this.routeInfo.categoryId === -1) {
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        // page
        this.$router.push({
          name: "CategoryBlog",
          query,
          params: {
            categoryId: this.routeInfo.categoryId,
          },
        });
      }
    },
  },
  //  watch 
  // $route(newVal,oldVal)
  // this.data:data(newVal,OldVal){}
  watch: {
    async $route() {
      this.$refs.blogList.scrollTop = 0; //
      this.isLoading = true; //loading
      //
      this.data = await getBlogs(
        this.routeInfo.categoryId,
        this.routeInfo.page,
        this.routeInfo.limit
      );
      if (this.data) {
        this.isLoading = false;
      }
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.blog-list-container {
  line-height: 1.7;
  position: relative;
  padding: 20px;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth; //
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    li {
      float: left;
      padding: 15px 0;
      border-bottom: 1px solid @gray;
      .thumb {
        float: left;
        margin-right: 15px;
        max-width: 200px;
        img {
          width: 100%;
          border-radius: 5px;
        }
      }
      h2 {
        margin: 0;
      }
      .message {
        font-size: 12px;
        color: @gray;
        span {
          margin-right: 15px;
        }
      }
      .desc {
        margin: 15px 0;
        font-size: 1em;
      }
    }
  }
}
@media screen and (max-width: 600px) {
  .blog-list-container {
    padding: .5em;
    li {
      .thumb {
        width: 20vw;
        height: 20vw;
        overflow: hidden;
      }
      h2 {
        font-size: 1em;
      }
    }
  }
}
</style>
