import getComponentRootDom from "./getComponentRootDom";
import Icon from "@/components/Icon";
import styles from "./alertMessage.module.less";

/** 
 * 
 * @param {String} content 
 * @param {String} type info error success warn
 * @param {HTMLElement} container 
 * @param {Number} duration 
 * @param {Function} callback 
*/
export default function (options = {}) {
  const content = options.content || "";
  const type = options.type || "info";
  const duration = options.duration || 2000;
  const container = options.container || document.body;


  // 
  const div = document.createElement('div'),
    iconDom = getComponentRootDom(Icon, { type });//vueDOM
  //  
  div.innerHTML = `
    <span class="${styles.icon}">${iconDom.outerHTML}</span>
    <div>${content}</div>
  `;

  //  ,
  const typeClassName = styles[`message-${type}`];//
  div.className = `${styles.message} ${typeClassName}`;

  // position,relative
  if(container !== document.body){//body
    if(getComputedStyle(container).position === "static"){
      container.style.position = "relative";
    }
  }
  

  // div
  container.appendChild(div);

  // reflow
  div.clientHeight;

  // 
  div.style.opacity = 1;
  div.style.transform = `translate(-50%,-50%)`;

  // 
  setTimeout(()=>{
    div.style.opacity = 0;
    div.style.transform = `translate(-50%,-50%) translateY(-25px)`;
    div.addEventListener(
      "transitionend",
      function(){
        div.remove();
        options.callback && options.callback();// 
      },
      {once:true}//
    );
  },duration);
}