<template>
  <div
    class="top-container"
    v-show="isShow"
    ref="top-container"
    @click="handleClick"
  >
    TOP
  </div>
</template>
<script>
export default {
  data() {
    return {
      isShow: false,
    };
  },
  created() {
    // mainScroll
    this.$bus.$on("mainScroll", this.handleShow);
  },
  methods: {
    // Top
    // dommainScrollthis.$emit("mainScroll",dom);
    // Detailthis.$emit("mainScroll",null);
    handleShow(dom) {
      if (!dom) {
        //domnull
        this.isShow = false;
        return;
      }
      this.isShow = dom.scrollTop > 800 ? true : false;
    },
    handleClick() {
      //  toTopScroll  0, 
      this.$bus.$emit("toTopScroll", 0);
    },
  },
  destoryed() {
    this.$bus.$off("mainScroll", this.handleShow);
  },
};
</script>
<style lang="less" scoped>
@import "~@/styles/var.less";
.top-container {
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  background: @primary;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  z-index: 999;
}
@media screen and (max-width: 600px) {
  .top-container {
    right: 3vw;
    bottom: 18vw;
    width: 3em;
    height: 3em;
    line-height: 3em;
    font-size: .6em;
  }
}
</style>