<template>
<!-- BlogDetail Comment BlogToc Loading  -->
  <Layout>
    <template #main>
      <div class="main-container" ref="mainContainer" @scroll="handleScroll">
        <!-- , -->
        <BlogDetail v-if="detailData" :blog="detailData" />
        <Comment
          v-if="commentsData"
          title=""
          :commentCount="commentsData.total"
          :list="commentsData.rows"
          @submit="handleSubmit"
          :isLoading="isLoadMore"
        /> 
        <div>
          <Loading v-if="isLoading" /><!-- main-containerloading -->
          <div class="no-more-comment" v-if="commentsData" >{{hasMore ? "...":""}}</div>
        </div>
      </div>
    </template>

    <template #right>
      <BlogToc v-if="detailData" :toc="detailData.toc" />
    </template>
  </Layout>
</template>

<script>
import Layout from "@/components/Layout"; //layout
import BlogDetail from "./components/BlogDetail"; //
import BlogToc from "./components/BlogToc"; //
import Comment from "@/components/Comment"; //
import Loading from "@/components/Loading";
import { getOneBlog, getComments, postComment } from "@/api/blog";
import alertMessage from "@/utils/alertMessage";
import toTop from "@/mixins/toTop";
export default {
  mixins:[toTop("mainContainer")],//
  components: {
    Layout,
    BlogDetail,
    BlogToc,
    Comment,
    Loading,
  },
  data() {
    return {
      detailData: null, //
      commentsData: null, //
      page:1,//
      isLoading: true,//loading
      isLoadMore:false,//loading
    };
  },
  computed:{
    // :
    hasMore(){
      if(this.commentsData){
        return this.commentsData.rows.length < this.commentsData.total;
      }
    }
  },
  async created() {
    this.detailData = await getOneBlog(this.$route.params.id); //
    // , /404 "*" NotFound
    if(!this.detailData){
      this.$router.push("/404");
      return;
    }
    // loading
    if (this.detailData) {
      this.isLoading = false;
      this.commentsData = await getComments(this.$route.params.id, this.page, 10); //
    }
  },
  methods: {
    handleScroll(){
      const dom = this.$refs.mainContainer;
      // dom
      if(this.isLoadMore || ! dom) {return}
      // 50px
      if(dom.scrollHeight - (dom.scrollTop + dom.clientHeight) < 50 ){
        this.loadMoreComment();
      }
    },
    async loadMoreComment(){
      if(!this.hasMore) { return } ;//
      this.isLoadMore = true;
      this.page ++;//
      const newComments = await getComments(this.$route.params.id,this.page,10);
      // 
      this.commentsData.rows =this.commentsData.rows.concat(newComments.rows);
      this.commentsData.total = newComments.total;//total
      this.isLoadMore = false;
    },
    // submit,
    async handleSubmit(formData, callBack) {
      const msg = await postComment({ ...formData,blogId:this.$route.params.id });
      this.commentsData.rows.unshift(msg);//
      this.commentsData.total ++;//++
      callBack(); //
      alertMessage({ content: "", type: "success" });//
    },
  },
};
</script>
<style scoped lang='less'>
@import "~@/styles/var.less";
.main-container {
  position: relative;
  height: 100%;
  overflow-y: scroll;
  padding: 20px;
  scroll-behavior: smooth;
  .no-more-comment{
    width:100%;
    height: 50px;
    font: 1.5em;
    color: @gray;
    text-align: center;
  }
}
</style>