/**
 * 
 * 
 *  \  : ;
 *  ?  : 01  
 *  .  : 
 *  +  : 
 *  (\?.+)? : 01
 * 
 * mockMock.mock("/api/blog","get",function(options){})
 *  options
 *    options:{
 *      body: null
 *      type: "GET"
 *      url: "/api/blog?page=1&limit=20&categoryid=-1"
 *    }
 *  
 * querystring
 *  url
 *  url:{
 *    /api/blog?page: "1",
 *    categoryid: "-1",
 *    limit: "20"
 *  }
 *  
 */
Mock.mock(/^\/api\/blog(\?.+)?$/, "get", function (options) {
  const query = qs.parse(options.url);//url
  return Mock.mock({
    code: 0,
    msg: "",
    data: {// 
      "total|2000-3000": 0,
      [`rows|${query.limit || 10}`]: [
        {
          id: "@guid",//ID
          "title|1-5": "@ctitle",//
          description: "@cparagraph(2, 5)",//5-10
          category: {//
            "id|1-10": 0,
            name: "@id",
          },
          "scanNumber|0-3000": 0,//
          "commentNumber|0-300": 30,//
          //size,background,foreground,null
          "thumb|1": [Mock.Random.image("300x250", "#373737", "#fff", "Mock Image"), null],
          createDate: `@date('T')`,//
        },
      ],
    },
  });
});


