<template>
  <div class="home-container" ref="homeDom">
    <!--  -->
    <ul
      class="carousel-container"
      :style="{ marginTop: marginTop }"
      @wheel="handleWheel"
    >
      <!-- vuemarginTop===margin-top -->
      <li v-for="item in data" :key="item.id">
        <!-- props -->
        <Carouselitem :bannerDate="item" />
      </li>
    </ul>

    <!--  -->
    <div class="arrow arrowUp" v-show="index > 0" @click="switchTo(index - 1)">
      <Icon type="arrowUp" />
    </div>
    <!--  -->
    <div
      class="arrow arrowDown"
      v-show="index < data.length - 1"
      @click="switchTo(index + 1)"
    >
      <Icon type="arrowDown" />
    </div>
    <!--  -->
    <ul class="nav-cir">
      <li
        v-for="(item, i) in data"
        :key="item.id"
        :class="{ active: i === index }"
        ref="cir"
        @click="switchTo(i)"
      ></li>
    </ul>
    <!-- svg -->
    <Loading v-if="loading" />
  </div>
</template>

<script>
import Carouselitem from "./Carouselitem";
import Icon from "@/components/Icon";
import Loading from "@/components/Loading";
import { mapState } from "vuex";
export default {
  components: {
    Carouselitem,
    Icon,
    Loading,
  },
  data() {
    return {
      index: 0, //
      containerHeight: 0, //
      lock: true, //
    };
  },
  // :homeDommarginTop
  computed: {
    ...mapState("banner", ["loading", "data"]),
    marginTop() {
      return -this.index * this.containerHeight + "px";
    },
  },
  // dispatc
  async created() {
    this.$store.dispatch("banner/fetchData");
  },
  // dom:
  mounted() {
    // homeDom
    this.containerHeight = this.$refs.homeDom.clientHeight;
    // ,(destroyed)
    window.addEventListener("resize", this.handleResize);
  },
  // 
  methods: {
    switchTo(i) {
      this.index = i;
    },
    handleWheel(e) {
      if (this.lock) {
        // 
        this.lock = false;
        if (e.deltaY < 0) {
          //
          this.index === 0 ? null : this.index--;
        }
        if (e.deltaY > 0) {
          //
          this.index === this.data.length - 1 ? null : this.index++;
        }
      }
      // 300
      setTimeout(() => {
        this.lock = true;
      }, 300);
    },
    //  this.containerHeight 
    handleResize() {
      this.containerHeight = this.$refs.homeDom.clientHeight;
    },
  },
  // 
  destroyed() {
    removeEventListener("resize", this.handleResize);
  },
};
</script>

<style lang="less" scoped>
@import "~@/styles/var.less";
@import "~@/styles/mixin.less";

.home-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;

  // 
  .carousel-container {
    width: 100%;
    height: 100%;
    transition: 0.5s;
    li {
      width: 100%;
      height: 100%;
    }
  }
  // 
  .nav-cir {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    li {
      width: 0.6rem;
      height: 0.6rem;
      border-radius: 50%;
      border: 1px solid;
      margin: 10px;
      background: @gray;
      border: 1px solid #fff;
      transition: 0.5s;
      cursor: pointer;
      box-sizing: border-box;
      &.active {
        background: @words;
      }
    }
  }
  // 
  .arrow {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-size: 2vw;
    font-weight: bold;
    color: @gray;
    cursor: pointer;
    animation: jump 2s infinite;
  }
  // 
  .arrowUp {
    top: 20px;
  }
  // 
  .arrowDown {
    bottom: 20px;
  }
  // 
  @keyframes jump {
    0% {
      transform: translate(-50%, 5px);
    }
    50% {
      transform: translate(-50%, -5px);
    }
    100% {
      transform: translate(-50%, 5px);
    }
  }
}
@media screen and (max-width: 600px) {
  .home-container {
    .arrow {
      font-size: 7vw;
    }
    .arrowUp{
      top: 18vw;
    }
    .arrowDown{
      bottom: 18vw;
    }
  }
}
</style>