import axios from "axios";
import alertMessage from "../utils/alertMessage";

const ins = axios.create();//axios

// response,use(func)func
ins.interceptors.response.use((resp)=>{
  // code === 0 
  if(resp.data.code !==0 ){
    alertMessage({
      content:resp.data.msg,
      type:"error",
      duration:1500,
    });
    return null;
  }
  return resp.data.data;
});

export default ins;//insaxios