<template>
  <div class="blog-category-container">
    <h3></h3>
    <RightList :list="list" @select="handleSelect" />
    <Loading v-show="isLoading" />
  </div>
</template>

<script>
import RightList from "./RightList";
import { getBlogType } from "@/api/blog";
import Loading from "@/components/Loading";

export default {
  components: {
    RightList,
    Loading,
  },
  data() {
    return {
      isLoading: true,
      data: [],
    };
  },
  async created() {
    this.data = await getBlogType();
    if (this.data) {
      this.isLoading = false;
    }
  },
  computed: {
    routeInfo() {
      const categoryId = this.$route.params.categoryId || -1;
      const limit = this.$route.query.limit || 10;
      return {
        categoryId,
        limit,
      };
    },
    // list[]: data,listisSelect
    list() {
      // :totalArticleCount
      const totalArticleCount = this.data.reduce(
        (a, b) => a + b.articleCount, 0 //a0
      );
      const list = [
        { name: "", id: -1, articleCount: totalArticleCount },
        ...this.data, //
      ];
      // listisSelect:
      const result = list.map((item) => ({
        ...item, // 
        currentArticleCount:`${item.articleCount}`,//
        isSelect: item.id === this.routeInfo.categoryId,//
      }));
      return result; //isSelect
    },
  },
  methods: {
    // push::
    handleSelect(item) { //itemRightList
      const query = {
        page: 1,//
        limit: this.routeInfo.limit,
      };
      // params:categoryId
      if (item.id === -1) {
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        // 
        this.$router.push({
          name: "CategoryBlog",
          query,
          params: {
            categoryId: item.id,
          },
        });
      }
    },
  },
};
</script>

<style lang="less" scoped>
.blog-category-container {
  position: relative;
  width: 180px;
  height: 100%;
  box-sizing: border-box;
  padding: 10px;
  overflow-y: auto;
  h3 {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1em;
  }
}
@media screen and (max-width:600px) {
  .blog-category-container{
    width: 22vw;
    padding: 5vw 0 0 0;
    margin: 0;
    h3{
      display: none;
    }
  }
}
</style>