<template>
  <ul class="right-sidebar-container">
    <li v-for="item in list" :key="item.name">
      <span
        class="item-name"
        :class="{ active: item.isSelect }"
        @click="handleClick(item)"
      >
        {{ item.name }}
      </span>
      <span
        :class="{ active: item.isSelect }"
        class="currentArticleCount"
        @click="handleClick(item)"
      >
        {{ item.currentArticleCount }}
      </span>

      <!--  -->
      <RightList :list="item.children" @select="handleClick" />
    </li>
  </ul>
</template>

<script>
export default {
  // 
  name: "RightList",

  /* li
 [{
    name:"xxx",
    isSelect:true,
    children:[{
      name:"xxx",isSelect:true
    }],
  }] 
*/
  props: {
    list: {
      type: Array,
      default: () => [], //
    },
  },
  methods: {
    /* 
    selectitem[] */
    handleClick(item) {
      // 
      if (!item.isSelect) {
        this.$emit("select", item);
      }
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/styles/var.less";
.right-sidebar-container {
  margin-left: 1em;
  cursor: pointer;
  li {
    span {
      height: 40px;
      line-height: 40px;
      font-size: 15px;
      cursor: pointer;
      &.active {
        color: @warn;
        font-weight: bold;
      }
    }
    .currentArticleCount {
      color: @lightWords;
      font-size: 10px;
      margin-left: 5px;
    }
  }
}
@media screen and (max-width: 600px) {
  .right-sidebar-container {
    text-align: center;
    margin: 0;
    .currentArticleCount {
      display: none;
    }
  }
}
</style>