<template>
  <img
    class="avatar-container"
    :src="url"
    :style="{
      width: width + 'px',
      height: height + 'px',
    }"
  />
</template>

<script>
// ":"js
export default {
  props: {
    url: {
      type: String, // url
      required: true, //url
    },
    width:{
     type: Number,//number 
     default:150,//150
    },
    height:{
      type:Number,
      default:150,
    },
  },
};
</script>
<style scoped>/* scoped: */
  .avatar-container{
    border-radius: 50%;
    display: block;
    object-fit: cover;/*  */
  }
</style>
